 4px;\r\n  --s: 8px;\r\n  --m: 16px;\r\n  --l: 24px;\r\n  --lx: 32px;\r\n  --lxx: 40px;\r\n}\r\n\r\nsub,\r\nsup {\r\n  line-height: 0;\r\n  position: relative;\r\n  vertical-align: baseline;\r\n}\r\n\r\nsup {\r\n  top: -0.5em;\r\n}\r\n\r\nsub {\r\n  bottom: -0.25em;\r\n}\r\n\r\nhtml {\r\n  scroll-behavior: smooth;\r\n  font-size: 16px;\r\n}\r\n\r\nbody {\r\n  text-rendering: optimizeLegibility !important;\r\n  /* font-smoothing: antialiased; */\r\n  -webkit-font-smoothing: antialiased !important;\r\n  -moz-osx-font-smoothing: grayscale !important;\r\n  font-family: poppins, -apple-system, BlinkMacSystemFont, \"Segoe UI\", Roboto,\r\n    Oxygen, Ubuntu, Cantarell, \"Open Sans\", \"Helvetica Neue\", sans-serif;\r\n}\r\n\r\nul {\r\n  list-style: none;\r\n}\r\n\r\na {\r\n  color: white;\r\n  text-decoration: none;\r\n  display: block;\r\n  padding: var(--s) var(--m);\r\n}\r\n\r\n.btn {\r\n  font-size: inherit;\r\n  font-family: inherit;\r\n  background: white;\r\n  border: 3px solid black;\r\n  border-radius: 40px;\r\n}\r\n\r\n[class*=\"__container\"] {\r\n  width: 1900px;\r\n  max-height: 1000px;\r\n  min-height: 400px;\r\n  /* outline: 15px solid salmon; */\r\n}\r\n\r\nbody {\r\n  background: white;\r\n  color: white;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\nsection,\r\nheader,\r\nfooter {\r\n  width: 100%;\r\n  /* outline: 15px solid greenyellow; */\r\n  display: flex;\r\n  justify-content: center;\r\n}\r\n\r\n/* \r\n    =======================================\r\n    =       Real code begins from here    =\r\n    =======================================\r\n     */\r\n\r\n/* header */\r\n\r\nheader {\r\n  position: relative;\r\n  overflow: hidden;\r\n  flex-direction: row;\r\n  flex-wrap: wrap;\r\n  justify-content: center;\r\n  min-height: 100vh;\r\n  /* min-height: fit-content; */\r\n}\r\n\r\n.navbar {\r\n  align-self: flex-start;\r\n  flex: 0 0 100%;\r\n  display: flex;\r\n  justify-content: center;\r\n}\r\n\r\n.navbar__container {\r\n  display: flex;\r\n  justify-content: center;\r\n  flex-direction: row-reverse;\r\n  align-items: center;\r\n  min-height: 100px;\r\n  /* outline: 5px solid plum; */\r\n}\r\n\r\n.navbar__toggle {\r\n  width: 40px;\r\n  height: 40px;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: space-around;\r\n  align-items: center;\r\n  /* outline: 5px solid plum; */\r\n  cursor: pointer;\r\n  visibility: hidden;\r\n  /* background: plum; */\r\n  position: absolute;\r\n  top: 2rem;\r\n  right: 3rem;\r\n  z-index: 6;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.bar {\r\n  display: block;\r\n  background: white;\r\n  height: 5px;\r\n  width: 100%;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n/* navbar menu */\r\n\r\n.navbar__menu {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  transition: all 0s ease;\r\n}\r\n\r\n.navbar__item {\r\n  text-align: center;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  width: 125px;\r\n}\r\n\r\n.navbar__link {\r\n  width: 100%;\r\n  height: 100%;\r\n  text-transform: uppercase;\r\n}\r\n\r\n.navbar__link:hover {\r\n  text-decoration-line: underline;\r\n  text-decoration-color: var(--khaki);\r\n  text-decoration-thickness: 4px;\r\n  text-underline-offset: var(--m);\r\n}\r\n\r\n/* info section */\r\n.info {\r\n  flex: 1 0 100%;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.info__container {\r\n  position: relative;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  /* height: 100vh; */\r\n  /* max-height: 900px; */\r\n}\r\n\r\n.info__img {\r\n  /* outline: 5px solid plum; */\r\n  margin-top: var(--lxx);\r\n  width: 250px;\r\n  height: 250px;\r\n  border-radius: 50%;\r\n  clip-path: ellipse(45% 45% at 50% 50%);\r\n  filter: drop-shadow(0 15px 24px rgba(0, 0, 0, 0.419));\r\n}\r\n\r\n.info__img img {\r\n  width: 105%;\r\n}\r\n\r\n.info__text {\r\n  margin-top: var(--lxx);\r\n  width: 100%;\r\n  text-align: center;\r\n  /* outline: 5px solid plum; */\r\n}\r\n\r\n.info__text h1 {\r\n  /* outline: 5px solid plum; */\r\n  width: 100%;\r\n  font-size: 5rem;\r\n  line-height: 5.5rem;\r\n  /* font-family: serif; */\r\n}\r\n\r\n.info__text p {\r\n  margin-top: var(--m);\r\n  font-size: 3rem;\r\n  line-height: 3.5rem;\r\n  /* font-family: serif; */\r\n}\r\n\r\n.info__social-media-link {\r\n  margin-top: var(--lxx);\r\n  width: 100%;\r\n  height: 100%;\r\n  filter: invert(1);\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.info__social-media-link a {\r\n  display: flex;\r\n  justify-content: center;\r\n  width: 4rem;\r\n  height: 4rem;\r\n}\r\n\r\n.go-down-link {\r\n  margin-top: var(--lxx);\r\n  width: 100%;\r\n  text-align: center;\r\n  display: flex;\r\n  justify-content: center;\r\n}\r\n\r\n.go-down-link a img {\r\n  display: inline-block;\r\n  width: 40px;\r\n  animation: wobble 2s infinite alternate-reverse;\r\n}\r\n\r\n@keyframes wobble {\r\n  from {\r\n    transform: translateY(0);\r\n  }\r\n\r\n  to {\r\n    transform: translateY(10px);\r\n  }\r\n}\r\n\r\n.bg-img {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  z-index: -2;\r\n  width: 100%;\r\n  height: 100%;\r\n}\r\n\r\n.bg-img img {\r\n  width: 100%;\r\n  height: 100%;\r\n  /* display: block; */\r\n  object-fit: cover;\r\n}\r\n\r\n.overlay {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  z-index: -1;\r\n  background: rgba(0, 0, 0, 0.501);\r\n  width: 100%;\r\n  height: 100%;\r\n}\r\n\r\n/*\r\n    who am i section\r\n    */\r\n.whoami {\r\n  background: var(--blue);\r\n}\r\n\r\n.whoami__container {\r\n  padding: var(--lxx) 0 0;\r\n  height: 600px;\r\n  display: flex;\r\n  flex-direction: row-reverse;\r\n  flex-wrap: wrap;\r\n  justify-content: center;\r\n  align-items: center;\r\n  overflow: hidden;\r\n}\r\n\r\n.whoami__txt {\r\n  text-align: center;\r\n  flex: 1 0 60%;\r\n  padding: var(--l);\r\n}\r\n\r\n.whoami__txt h1 {\r\n  font-size: clamp(4rem, 2vw, 6rem);\r\n  line-height: 6rem;\r\n}\r\n\r\n.whoami__img {\r\n  flex: 0 1 40%;\r\n  height: 100%;\r\n  margin: 0 auto;\r\n  object-fit: contain;\r\n}\r\n\r\n/* popular films section */\r\n.film {\r\n  background: var(--black);\r\n}\r\n\r\n.film__container {\r\n  max-height: 100%;\r\n}\r\n\r\n.film__heading {\r\n  padding: 3rem 2rem;\r\n  font-size: 5rem;\r\n  height: 400px;\r\n  background: var(--khaki);\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  text-transform: capitalize;\r\n}\r\n\r\n/* other film section global settings */\r\n.film__one,\r\n.film__two,\r\n.film__three {\r\n  display: flex;\r\n  justify-content: space-evenly;\r\n  flex-direction: row;\r\n  flex-wrap: wrap;\r\n  padding: var(--lxx) 0;\r\n}\r\n\r\n.film [class*=\"__link\"] img {\r\n  width: 100%;\r\n  height: 100%;\r\n  transition: all 0.3s ease;\r\n  object-fit: contain;\r\n}\r\n\r\n.film [class*=\"__link\"] img:hover {\r\n  transition: all 0.3s ease;\r\n  filter: drop-shadow(0px 35px 45px rgba(0, 0, 0, 0.438));\r\n}\r\n\r\n.film [class*=\"__content\"] {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.film__txt-content {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: space-between;\r\n  /* outline: 5px solid rgb(114, 116, 250); */\r\n  padding: var(--l);\r\n}\r\n\r\n.film__txt-content h2 {\r\n  font-size: 3.5rem;\r\n  line-height: 4rem;\r\n}\r\n\r\n.film__rating {\r\n  margin-top: var(--l);\r\n  display: flex;\r\n  justify-content: flex-start;\r\n  align-items: center;\r\n}\r\n\r\n.film__rating span:nth-child(1) {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.film__rating span:nth-child(2) {\r\n  padding-left: var(--m);\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.film__txt-content p {\r\n  margin-top: var(--l);\r\n  font-size: 1.5rem;\r\n  line-height: 2rem;\r\n}\r\n\r\n.film__rating img {\r\n  filter: invert(1);\r\n  width: 18px;\r\n}\r\n\r\n.film__btn {\r\n  border: none;\r\n  margin: 2rem 0;\r\n  padding: 1rem 1rem;\r\n  max-width: 300px;\r\n  min-height: 50px;\r\n  /* display: flex;  */\r\n  /* justify-content: space-evenly; */\r\n  /* align-items: center; */\r\n  transition: all 0.4s ease;\r\n  cursor: pointer;\r\n}\r\n\r\n.film__btn:hover {\r\n  transition: all 0.4s ease;\r\n  transform: translateY(-8px);\r\n  box-shadow: -5px 9px 32px rgba(25, 25, 26, 0.506);\r\n}\r\n\r\n.film__btn a {\r\n  padding: 0;\r\n  /* margin: 0; */\r\n  font-size: 1.4rem;\r\n  line-height: 1.9rem;\r\n  color: var(--black);\r\n  width: 100%;\r\n  height: 100%;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n.film__btn span {\r\n  /* display: inline-block; */\r\n  /* height: 100%; */\r\n  /* padding-left: 5px; */\r\n  /* outline: 4px solid red; */\r\n}\r\n\r\n.film__btn span:nth-child(2) {\r\n  padding-left: var(--s);\r\n  display: inline-flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  /* filter: invert(1); */\r\n}\r\n\r\n.film__btn img {\r\n  /* margin: 0 auto; */\r\n  height: 2rem;\r\n  width: 2rem;\r\n}\r\n\r\n/* film 1 section */\r\n\r\n.film__one {\r\n  background: var(--light-brown);\r\n  padding-bottom: 3rem;\r\n}\r\n\r\n.film-1__link {\r\n  flex: 1 1 40%;\r\n\r\n  width: 400px;\r\n  height: 600px;\r\n  transform: translateY(-15%);\r\n  filter: drop-shadow(9px 15px 25px rgba(0, 0, 0, 0.412));\r\n}\r\n\r\n/* film__content is has display flex and is in global settings */\r\n\r\n.film-1__content {\r\n  flex: 1 1 40%;\r\n}\r\n\r\n/* film 2 section */\r\n.film__two {\r\n  background: var(--dark-brown);\r\n  padding-bottom: 3rem;\r\n  flex-direction: row-reverse;\r\n}\r\n\r\n.film-2__link {\r\n  flex: 1 1 40%;\r\n\r\n  width: 400px;\r\n  height: 600px;\r\n  transform: translateY(-15%);\r\n  filter: drop-shadow(9px 15px 25px 